<template>
    <div id="single-blog">
        <h1>{{blog.title}}</h1>
        <article>{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <ul>
            <li v-for="(category,index) in blog.categories" :key="index">
                {{category}}
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:"single-blog",
    data(){
        return{
            id:this.$route.params.id,
            blog:{}
        }
    },
    created(){
        this.$http.get('https://vue-demo01.firebaseio.com/posts/'+this.id+'.json').then((response)=>{
            console.log(response);
            return response.json();
            // this.blog=response.body;
        }).then(data=>{
            console.log(data)
            this.blog=data;
        })
    }
}
</script>
<style>
    #single-blog{
        max-width: 960px;
        margin: 0 auto;
        padding: 20px;
        border: 1px dotted #ccc;
        background: #f1f1f1;
    }
</style>



