<template>
  <div class="add-blog">
    <h2>添加博客</h2>
    <form v-if="!submited">
        <label for="">博客标题</label>
        <input type="text" v-model="blog.title" required>

        <label for="">博客内容</label>
        <textarea v-model="blog.content" name="" id=""></textarea>

        <div id="checkboxs">
            <label for="">Vue.js</label>
            <input type="checkbox" value="Vue.js" v-model="blog.categories">
            <label for="">Node.js.js</label>
            <input type="checkbox" value="Node.js" v-model="blog.categories">
            <label for="">React.js</label>
            <input type="checkbox" value="React.js" v-model="blog.categories">
            <label for="">Angular.js</label>
            <input type="checkbox" value="Angular.js" v-model="blog.categories">
            
        </div>
        <label for="">作者：</label>
        <select v-model="blog.author">
            <option v-for="(author,index) in authors" :key="index">
                {{author}}
            </option>
        </select>
        <button v-on:click.prevent="postMethods">添加博客</button>
    </form>
    <div v-if="submited">
        <h3>你的博客发布成功！</h3>
    </div>
    <div id="preview">
        <h3>博客总览</h3>
        <p>博客标题:{{blog.title}}</p>
        <p>博客内容</p>
        <p>{{blog.content}}</p>
        <p>博客分类：</p>
        <ul>
            <li v-for="(category,index) in blog.categories" :key="index" >
                {{category}}
            </li>
        </ul>
        <p>作者:{{blog.author}}</p>
    </div>
</div>
</template>

<script>
export default {
  name: "add-blog",
  data(){
    return {
      blog: {
        title: "",
        content: "",
        categories:[],
        author:""
      },
      authors:["herry","micke","jack"], 
      submited:false
    }
  },
  methods:{
      postMethods:function(){
          this.$http.post('https://vue-demo01.firebaseio.com/posts.json',this.blog).then((response)=>{
              console.log(response);
              this.submited=true;
          })
      }
  }
}
</script>
<style scoped>
    .add-blog *{
        box-sizing: border-box;
    }
    .add-blog{
        margin:20px auto;
        max-width: 500px;
        padding: 20px;
        text-align: center;
    }
    label{
        display:block;
        margin: 20px 0 10px;
    }
    input[type='text'],textarea,select{
        display: block;
        width: 100%;
        padding: 20px;
    }
    textarea{
        height: 220px;
    }
    #checkboxs label{
        display: inline-block;
        margin-top: 0;
    }
    #checkboxs input{
        display: inline-block;
        margin-right: 10px;
    }
    button{
        display: block;
        margin: 20px 0;
        background: crimson;
        border: 0;
        border-radius: 10px;
        cursor: pointer;;
        font-size: 18px;
        color: #fff;
    }
    #preview{
        padding: 10px 20px;
        border: 1px dotted #ccc;
        margin: 30px 0;
    }
    h3{
        margin-top: 10px;
    }
</style>



