<template>
  <div v-them:colum=" 'narrow' " id="show-blogs">
    <h1>博客总览</h1>
    <input type="text" v-model="search" placeholder="搜索">
   <div v-for="(blog,index) in filteredBlogs" :key="index" class="single-blog">
    <router-link v-bind:to=" '/blog/'+blog.id ">
        <h2 v-rainbow>{{blog.title |to-uppercase}}</h2>
    </router-link>
    <article>{{blog.content | snippet}}</article>
</div>
  </div>
</template>

<script>
export default {
  name: "show-blogs",
  data() {
    return {
      blogs: [],
      search: ""
    };
  },
  created() {
    //'http://jsonplaceholder.typicode.com/posts' ...........网络请求
    this.$http.get("https://vue-demo01.firebaseio.com/posts.json").then(response => {
      // console.log(response.json());
      return response.json()
      // this.blogs = response.body.slice(0, 10);
      // console.log(this.blogs);
    }).then(data=>{
      var blogsArray=[];
      for(let key in data){
        // console.log(key);
        // console.log(data[key]);
        data[key].id=key;
        blogsArray.push(data[key]);
      }
      // console.log(blogsArray)
      this.blogs=blogsArray;
      console.log(this.blogs);
    })
  },
  computed: {
    filteredBlogs: function() {
      return this.blogs.filter(blog => {
        return blog.title.match(this.search);
      });
    }
  },
  filters: {
    // 'to-uppercase':function(value){
    //    return value.toUppercase();
    // },
    toUppercase(value) {
      return value.toUpperCase();
    }
  },
  directives: {
    rainbow: {
      bind(el, binding, vnode) {
        el.style.color = el.style.color =
          "#" +
          Math.random()
            .toString(16)
            .slice(2, 8);
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#show-blogs {
  max-width: 800px;
  margin: 0 auto;
}
.single-blog {
  padding: 20px;
  margin: 20px 0;
  box-sizing: border-box;
  background: #eee;
}
#show-blogs a{
  color: #444;
  text-decoration: none;
}
input[type="text"]{
  padding: 8px;
  width: 100%;
  box-sizing: border-box;

}
</style>



